'use client';

import React, { useEffect, useState } from 'react';
import Image from 'next/image';
import { useAuth } from '@/lib/supabase/auth-provider';
import { useAuthGuard } from '@/hooks/useAuthGuard';
import { useLanguageStore } from '@/stores/languageStore';
import { VoteItem } from '@/types/interfaces';
import { getLocalizedString } from '@/utils/api/strings';

interface VoteDialogProps {
  isOpen: boolean;
  onClose: () => void;
  onVote: () => void;
  selectedArtist: VoteItem | null;
  votes: number;
  setVotes: (votes: number) => void;
}

/**
 * Ìà¨Ìëú Îã§Ïù¥ÏñºÎ°úÍ∑∏ Ïª¥Ìè¨ÎÑåÌä∏
 * Î°úÍ∑∏Ïù∏Îêú ÏÇ¨Ïö©ÏûêÏóêÍ≤åÎßå ÌëúÏãúÎêòÎ©∞, Í∞ïÌôîÎêú Ïù∏Ï¶ù Í≤ÄÏ¶ùÏùÑ ÏàòÌñâÌï©ÎãàÎã§.
 */
const VoteDialog: React.FC<VoteDialogProps> = ({
  isOpen,
  onClose,
  onVote,
  selectedArtist,
  votes,
  setVotes,
}) => {
  const { isAuthenticated, isLoading, user, session } = useAuth();
  const { t } = useLanguageStore();
  const { withAuth } = useAuthGuard({
    // Ìà¨ÌëúÎ≥Ñ ÎßûÏ∂§ Î°úÍ∑∏Ïù∏ Î©îÏãúÏßÄ
    customLoginMessage: {
      title: t('vote.login_required.title'),
      description: selectedArtist?.artist?.name
        ? t('vote.login_required.description_with_artist', { 
            artistName: getLocalizedString(selectedArtist.artist.name) 
          })
        : t('vote.login_required.description'),
      loginText: t('vote.login_required.login_button'),
      cancelText: t('vote.login_required.cancel_button'),
    }
  });
  const [authVerified, setAuthVerified] = useState(false);
  const [verificationError, setVerificationError] = useState<string | null>(null);

  /**
   * Í∞ïÌôîÎêú Ïù∏Ï¶ù ÏÉÅÌÉú Í≤ÄÏ¶ù
   */
  const verifyAuthentication = async () => {
    try {
      console.log('üîç [VoteDialog] Ïù∏Ï¶ù ÏÉÅÌÉú Í≤ÄÏ¶ù ÏãúÏûë');

      // 1. Í∏∞Î≥∏ Ïù∏Ï¶ù ÏÉÅÌÉú Ï≤¥ÌÅ¨
      if (!isAuthenticated || !user || !session) {
        console.warn('‚ùå [VoteDialog] Í∏∞Î≥∏ Ïù∏Ï¶ù ÏÉÅÌÉú Ïã§Ìå®');
        setAuthVerified(false);
        setVerificationError('Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§.');
        return false;
      }

      // 2. ÏÑ∏ÏÖò ÎßåÎ£å Ï≤¥ÌÅ¨
      if (session.expires_at) {
        const expiryTime = new Date(session.expires_at * 1000);
        const now = new Date();
        if (now >= expiryTime) {
          console.warn('‚è∞ [VoteDialog] ÏÑ∏ÏÖòÏù¥ ÎßåÎ£åÎê®');
          setAuthVerified(false);
          setVerificationError('ÏÑ∏ÏÖòÏù¥ ÎßåÎ£åÎêòÏóàÏäµÎãàÎã§. Îã§Ïãú Î°úÍ∑∏Ïù∏Ìï¥Ï£ºÏÑ∏Ïöî.');
          onClose(); // Îã§Ïù¥ÏñºÎ°úÍ∑∏ ÏûêÎèô Îã´Í∏∞
          return false;
        }
      }

      // 3. ÏÑúÎ≤Ñ Ïù∏Ï¶ù ÏÉÅÌÉú Í≤ÄÏ¶ù
      try {
        const response = await fetch('/api/auth/verify', {
          method: 'GET',
          credentials: 'include',
        });

        if (!response.ok) {
          console.warn('üö´ [VoteDialog] ÏÑúÎ≤Ñ Ïù∏Ï¶ù Í≤ÄÏ¶ù Ïã§Ìå®');
          setAuthVerified(false);
          setVerificationError('Ïù∏Ï¶ù ÏÉÅÌÉúÎ•º ÌôïÏù∏Ìï† Ïàò ÏóÜÏäµÎãàÎã§.');
          onClose(); // Îã§Ïù¥ÏñºÎ°úÍ∑∏ ÏûêÎèô Îã´Í∏∞
          return false;
        }

        const data = await response.json();
        if (!data.valid) {
          console.warn('‚ùå [VoteDialog] ÏÑúÎ≤ÑÏóêÏÑú Ïù∏Ï¶ù Î¨¥Ìö® ÏùëÎãµ');
          setAuthVerified(false);
          setVerificationError('Ïù∏Ï¶ùÏù¥ Ïú†Ìö®ÌïòÏßÄ ÏïäÏäµÎãàÎã§.');
          onClose(); // Îã§Ïù¥ÏñºÎ°úÍ∑∏ ÏûêÎèô Îã´Í∏∞
          return false;
        }
      } catch (error) {
        console.warn('‚ö†Ô∏è [VoteDialog] ÏÑúÎ≤Ñ Ïù∏Ï¶ù Í≤ÄÏ¶ù Ïò§Î•ò:', error);
        // ÎÑ§Ìä∏ÏõåÌÅ¨ Ïò§Î•òÎäî ÌóàÏö©ÌïòÎêò Í≤ΩÍ≥† ÌëúÏãú
        setVerificationError('ÎÑ§Ìä∏ÏõåÌÅ¨ Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§. Ìà¨ÌëúÏóê Î¨∏Ï†úÍ∞Ä ÏûàÏùÑ Ïàò ÏûàÏäµÎãàÎã§.');
      }

      console.log('‚úÖ [VoteDialog] Ïù∏Ï¶ù ÏÉÅÌÉú Í≤ÄÏ¶ù ÏÑ±Í≥µ');
      setAuthVerified(true);
      setVerificationError(null);
      return true;
    } catch (error) {
      console.error('üí• [VoteDialog] Ïù∏Ï¶ù Í≤ÄÏ¶ù Ï§ë Ïò§Î•ò:', error);
      setAuthVerified(false);
      setVerificationError('Ïù∏Ï¶ù ÌôïÏù∏ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.');
      onClose(); // Îã§Ïù¥ÏñºÎ°úÍ∑∏ ÏûêÎèô Îã´Í∏∞
      return false;
    }
  };

  // Îã§Ïù¥ÏñºÎ°úÍ∑∏Í∞Ä Ïó¥Î¶¥ ÎïåÎßàÎã§ Ïù∏Ï¶ù ÏÉÅÌÉú Í≤ÄÏ¶ù
  useEffect(() => {
    if (isOpen && !isLoading) {
      verifyAuthentication();
    }
  }, [isOpen, isLoading, isAuthenticated, user, session]);

  // Ïù∏Ï¶ùÎêòÏßÄ ÏïäÏùÄ ÏÇ¨Ïö©ÏûêÎäî Îã§Ïù¥ÏñºÎ°úÍ∑∏Î•º ÌëúÏãúÌïòÏßÄ ÏïäÏùå
  if (!isOpen || isLoading || !isAuthenticated || !authVerified) {
    return null;
  }

  // Í∞ïÌôîÎêú Ìà¨Ìëú Ïã§Ìñâ Ìï®Ïàò
  const handleVote = async () => {
    await withAuth(async () => {
      // Ìà¨Ìëú Ïã§Ìñâ Ï†Ñ Ïù∏Ï¶ù ÏÉÅÌÉú Ïû¨Í≤ÄÏ¶ù
      const isValid = await verifyAuthentication();
      if (!isValid) {
        console.warn('‚ùå [VoteDialog] Ìà¨Ìëú Ïã§Ìñâ Ï†Ñ Ïù∏Ï¶ù Í≤ÄÏ¶ù Ïã§Ìå®');
        return;
      }

      // Ïã§Ï†ú Ìà¨Ìëú Ïã§Ìñâ
      await onVote();
    });
  };

  return (
    <div className="fixed inset-0 bg-black/50 backdrop-blur-sm flex items-center justify-center z-50">
      <div className="bg-white rounded-lg p-6 w-full max-w-md mx-4">
        {/* Ìó§Îçî */}
        <div className="flex justify-between items-center mb-4">
          <h3 className="text-lg font-bold">
            {t('vote.dialog.title', { defaultValue: 'Ìà¨ÌëúÌïòÍ∏∞' })}
          </h3>
          <button
            onClick={onClose}
            className="text-gray-500 hover:text-gray-700"
            aria-label="Îã§Ïù¥ÏñºÎ°úÍ∑∏ Îã´Í∏∞"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-6 w-6"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M6 18L18 6M6 6l12 12"
              />
            </svg>
          </button>
        </div>

        {/* Ïù∏Ï¶ù Ïò§Î•ò ÌëúÏãú */}
        {verificationError && (
          <div className="mb-4 p-3 bg-yellow-100 border border-yellow-400 text-yellow-700 rounded">
            <p className="text-sm">{verificationError}</p>
          </div>
        )}

        {/* ÏÑ†ÌÉùÎêú ÏïÑÌã∞Ïä§Ìä∏ Ï†ïÎ≥¥ */}
        {selectedArtist && (
          <div className="mb-6">
            <div className="flex items-center space-x-4">
              <div className="w-16 h-16 rounded-full overflow-hidden border-2 border-white shadow-sm">
                {selectedArtist.artist?.image ? (
                  <Image
                    src={`${process.env.NEXT_PUBLIC_CDN_URL}/${selectedArtist.artist.image}`}
                    alt={getLocalizedString(selectedArtist.artist.name)}
                    width={64}
                    height={64}
                    className="w-full h-full object-cover"
                    unoptimized
                  />
                ) : (
                  <div className="w-full h-full bg-gray-200 flex items-center justify-center">
                    <span className="text-gray-600 text-xs">No Image</span>
                  </div>
                )}
              </div>
              <div>
                <p className="font-bold text-lg">
                  {getLocalizedString(selectedArtist.artist?.name)}
                </p>
                <p className="text-sm text-gray-600">
                  {t('vote.dialog.artist_label', { defaultValue: 'Ìà¨Ìëú ÎåÄÏÉÅ' })}
                </p>
              </div>
            </div>
          </div>
        )}

        {/* Ìà¨Ìëú Ïàò ÏÑ§Ï†ï */}
        <div className="mb-6">
          <label className="block text-sm font-medium text-gray-700 mb-2">
            {t('vote.dialog.vote_count_label', { defaultValue: 'Ìà¨Ìëú Ïàò' })}
          </label>
          <div className="flex items-center space-x-4">
            <button
              onClick={() => setVotes(Math.max(1, votes - 1))}
              className="p-2 rounded-full bg-gray-100 hover:bg-gray-200 disabled:opacity-50"
              disabled={votes <= 1}
              aria-label="Ìà¨Ìëú Ïàò Í∞êÏÜå"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-5 w-5"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M20 12H4"
                />
              </svg>
            </button>
            <input
              type="number"
              min="1"
              value={votes}
              onChange={(e) => setVotes(Math.max(1, parseInt(e.target.value) || 1))}
              className="w-20 text-center border rounded-lg p-2 focus:outline-none focus:ring-2 focus:ring-blue-500"
              aria-label="Ìà¨Ìëú Ïàò ÏûÖÎ†•"
            />
            <button
              onClick={() => setVotes(votes + 1)}
              className="p-2 rounded-full bg-gray-100 hover:bg-gray-200"
              aria-label="Ìà¨Ìëú Ïàò Ï¶ùÍ∞Ä"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-5 w-5"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M12 4v16m8-8H4"
                />
              </svg>
            </button>
          </div>
        </div>

        {/* Ïï°ÏÖò Î≤ÑÌäº */}
        <div className="flex justify-end space-x-4">
          <button
            onClick={onClose}
            className="px-4 py-2 text-gray-600 hover:text-gray-800 focus:outline-none focus:ring-2 focus:ring-gray-300 rounded"
          >
            {t('vote.dialog.cancel_button', { defaultValue: 'Ï∑®ÏÜå' })}
          </button>
          <button
            onClick={handleVote}
            disabled={!authVerified || !!verificationError}
            className="px-4 py-2 bg-primary text-white rounded-lg hover:bg-primary-dark focus:outline-none focus:ring-2 focus:ring-primary disabled:opacity-50 disabled:cursor-not-allowed"
          >
            {t('vote.dialog.vote_button', { defaultValue: 'Ìà¨ÌëúÌïòÍ∏∞' })}
          </button>
        </div>

        {/* Ïù∏Ï¶ù ÏÉÅÌÉú ÌëúÏãú (Í∞úÎ∞ú Î™®Îìú) */}
        {process.env.NODE_ENV === 'development' && (
          <div className="mt-4 p-2 bg-gray-100 rounded text-xs text-gray-600">
            <p>Auth Status: {isAuthenticated ? 'Authenticated' : 'Not Authenticated'}</p>
            <p>User ID: {user?.id || 'None'}</p>
            <p>Auth Verified: {authVerified ? 'Yes' : 'No'}</p>
          </div>
        )}
      </div>
    </div>
  );
};

export default VoteDialog;
